.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.ban-ao {
  position: relative;
  background-color: lightblue;
  border-radius: 1rem;
  width: 70rem;
  height: 50rem;
  display: grid;
  grid-template-columns: 60% 1fr;
}

.hinh {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.hinh img {
  width: 90%;
}

.noi-dung {
  position: relative;
  padding-top: 40%;
  padding-bottom: 40%;
}

.hinh-text {
  position: absolute;
  bottom: 20%;
  border: none;
  background-color: transparent;
  color: lightslategray;
  left: 25%;
  width: 50%;
  text-align: center;
}

.hinh-container {
  position: absolute;
  top: 20%;
  width: 50%;
  height: 50%;
  left: 25%;
  /* background-color: lime; */
  display: flex;
  align-items: center;
  justify-content: center;
}

.hinh-image {
  height: 50%;
  width: 60%;
  position: relative;
}

.hinh-image img {
  width: 100% !important;
  opacity: 0.8;
}

.customizable-text {
  margin-top: 2rem;
  font-weight: 500;
  color: gray;
  font-size: 1.4rem;
}

.file-input {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
}

.custom-text {
  margin-top: 0.8rem;
  margin-bottom: 0.1rem;
  background-color: lightgray;
  border-radius: 0.5rem;
  border: none;
  font-size: xx-large;
}

.photo-text {
  color: blue;
}

.photo-text:hover {
  color: cadetblue;
}

.c-font {
  font-weight: 650;
}

.xl-font {
  font-size: 4rem;
}

.lg-font {
  font-size: 2.2rem;
}

.sml-font {
  font-size: 1.5rem;
}
